import React from 'react';
import { useRouter } from 'next/router';

const Dashboard: React.FC = () => {
  const router = useRouter();

  return (
    <div className="min-h-screen bg-[#F5FBFF] flex items-center justify-center px-4">
      <div className="bg-white rounded-2xl shadow-xl p-8 max-w-md w-full space-y-6">
        <h2 className="text-2xl font-bold text-center text-gray-800">Admin Dashboard</h2>
        
        <div className="flex flex-col gap-4">
          <button
            onClick={() => router.push('/admin')}
            className="bg-[#757442] text-white py-3 rounded-xl hover:opacity-90 transition"
          >
            Dashboard
          </button>
          
          <button
            onClick={() => router.push('/admin/services')}
            className="bg-[#757442] text-white py-3 rounded-xl hover:opacity-90 transition"
          >
            Service
          </button>
          
          <button
            onClick={() => router.push('/admin/barbers')}
            className="bg-[#757442] text-white py-3 rounded-xl hover:opacity-90 transition"
          >
            Barber
          </button>
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
